code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(0, 0, 54, 0.918);
  padding-top: 50px;
  color: white;
  text-align: center;
}


.Cell {
  height: 100px;
  width: 100px;
  opacity: 80%;
  background-color: darkgray;
  /* background-image: radial-gradient(circle at center, darkgray , rgb(169,169,169) ,rgb(160, 160, 160) ); */
  transition: opacity 0.5s ease;
  border-radius: 1px;
}

.Cell-lit {
  opacity: 100%;
  background-color: white;
  /* background-image: radial-gradient(circle, rgb(255, 255, 255) , rgb(245, 245, 245) , rgb(219, 219, 219) ); */
}
:root {
  /* Base font size */
  font-size: 10px;

  /* Change neon color */
  --neon-color: rgb(81, 224, 210);
}

.Board-align{
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.Board {
  margin: 0 auto;
}
.Board-neon-text {
  
  margin: 20px;
  
  color: #fff;
  text-shadow: 0 0 12px  rgb(81, 224, 210);
  animation: neon 1s infinite;
  color:  rgb(81, 224, 210);
}

.Board-sign {
  
  margin: 20px;
  width: 20%;
  border: 3px solid black;
  border-radius: 10px;
  padding: 11px;
  text-shadow: 0 0 12px  rgb(81, 224, 210);
  color:  rgb(81, 224, 210);
  transform: rotate(-4deg);
  animation: neon 1s infinite;
  -webkit-user-select: none;
          user-select: none;
}

.Board-sign p {
  font-family: 'Pacifico', cursive;
  font-size: 39px;
  margin: 0;
}

.Board-sign b {
  font-family: 'Work Sans', sans-serif;
  font-weight: 900;
  font-size: 35px;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.Board-sign span {
  animation: flicker 6s infinite;
}

@keyframes neon {
  0%, 24%, 76%, 100% {
    box-shadow: 0 0 0 3px  rgb(81, 224, 210), inset 0 0 0 3px rgb(93, 52, 168), inset 0 0 12px 1px rgb(93, 52, 168);
  }
  25%, 75% {
    box-shadow: 0px 0px 0px 3px  rgb(81, 224, 210), 0px 0px 17px 1px  rgb(81, 224, 210), inset 0 0 0 3px rgb(93, 52, 168);
  }
}
