  }
  25%, 75% {
    box-shadow: 0px 0px 0px 3px red, 0px 0px 17px 1px red, inset 0 0 0 3px #2334ff78;
  }
} */

@keyframes flicker {
  25%, 30%, 60%, 67% { opacity: 1; }
  26%, 63% { opacity: .7; }
  27%, 62% { opacity: .8; }
  28% { opacity: .3; }
  61% { opacity: .4; }
  65% { opacity: .5; }
}

.Board-neonStagnant {
  box-shadow: 
        0 0 0.5rem #fff, 
        inset 0 0 0.5rem #fff,
        0 0 2rem var(--neon-color), 
        inset 0 0 2rem var(--neon-color),
        0 0 4rem var(--neon-color), 
        inset 0 0 4rem var(--neon-color);
}
.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

